@use '../base/variable' as v;
@use '../base/mixins' as m;


.formulario{
    display: flex;
    flex-direction: column;

    &__fieldset{
        border: none;
        margin-top: 1rem;
        padding: 0;
    }

    &__legend{
        color: v.$gris;
        font-size: 2.2rem;
        margin-bottom: 2rem;

    }

    &__campo{
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1.5rem 0;

        
    }
    &__contenedor-icono{
            display: flex;
            border-radius: .5rem;
            overflow: hidden;
    }

    &__icono{
        background-color: v.$gris-oscuro;
        width: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;

        i{
            color: v.$blanco;
            font-size: 2rem;
        }
    }

    &__label{
        display: block;
        font-size: 1.8rem;
        color: rbga(v.$negro, .8);
    }

    &__input, &__select{
        padding: 1rem;
        border: 1px solid darken(v.$gris-claro, 20%);
        border-radius: .5rem;

        &--file{
            border: none
        }
        &--sociales{
            @extend .formulario__input;
            flex: 1;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;

        }
    }

    &__radio{
        display: flex;
        gap: 2rem;
        
    }
    &__listado{
        @include m.tags;
            
        
    }

    &__texto{
        margin-top: 3rem;
    }

    &__imagen{
        max-width: 20rem;
    }

    &__submit{
        @include m.boton(v.$azul, v.$blanco);
        width: auto;
        align-self: flex-start;
            
        &--registrar, &--full{
            width: 100%;
        }
    }
}

.horas{
    @include m.reset-lista; 
        @include m.grid(1, 1rem);

        @include m.tablet {
            @include m.grid(2, 1rem);
        }
        &__hora{
            color: v.$azul;
            border: 1px solid v.$azul;
            border-radius: 1rem;
            padding: 1rem;
            text-align: center;
            transition: all 200ms ease;

            &:hover{
                cursor: pointer;
                background-color: v.$azul;
                color: v.$blanco;
            }

            &--seleccionada{
               
                background-color: v.$azul;
                color: v.$blanco;
            }

            &--deshabilitado{ 
                @extend .horas__hora;
                border: 1px solid v.$gris;
                color: v.$gris;
                opacity: .5;
                &:hover{
                    cursor: not-allowed;
                }
            }

           
        }
            
        }
    
