/** Media Queries **/
.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  color: rbga(#1a1b15, 0.8);
}
.formulario__input, .formulario__input--sociales, .formulario__select--sociales, .formulario__select {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
}
.formulario__input--file, .formulario__select--file {
  border: none;
}
.formulario__input--sociales, .formulario__select--sociales {
  flex: 1;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__radio {
  display: flex;
  gap: 2rem;
}
.formulario__listado {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario__listado > li {
  background-color: #007df4;
  color: #FFFFFF;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: bold;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
  align-self: flex-start;
}
.formulario__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.formulario__submit--registrar, .formulario__submit--full {
  width: 100%;
}

.horas {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .horas {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.horas__hora, .horas__hora--deshabilitado {
  color: #007df4;
  border: 1px solid #007df4;
  border-radius: 1rem;
  padding: 1rem;
  text-align: center;
  transition: all 200ms ease;
}
.horas__hora:hover, .horas__hora--deshabilitado:hover {
  cursor: pointer;
  background-color: #007df4;
  color: #FFFFFF;
}
.horas__hora--seleccionada {
  background-color: #007df4;
  color: #FFFFFF;
}
.horas__hora--deshabilitado {
  border: 1px solid #64748B;
  color: #64748B;
  opacity: 0.5;
}
.horas__hora--deshabilitado:hover {
  cursor: not-allowed;
}

.acciones {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.acciones__enlace {
  color: #1a1b15;
  font-size: 1.4rem;
  font-weight: 300;
}
.acciones--centrar {
  justify-content: center;
}

.header {
  min-height: 60rem;
  background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.5)), image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: 400px, 400px, cover, cover;
  background-position: top left, bottom right, center center, center center;
}
.header__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__navegacion {
  padding: 5rem 0;
  display: flex;
  justify-content: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    justify-content: flex-end;
  }
}
.header__enlace, .header__submit {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: bold;
  text-transform: uppercase;
}
.header__enlace:hover, .header__submit:hover {
  cursor: pointer;
  color: #007df4;
}
.header__form {
  display: flex;
}
.header__contenido {
  padding-top: 10rem;
}
@media (min-width: 768px) {
  .header__contenido {
    padding: 5rem;
  }
}
.header__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 7rem);
  background: linear-gradient(to right, #007df4, #00c8c2);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
}
@media (min-width: 768px) {
  .header__logo {
    text-align: left;
    display: inline-block;
  }
}
.header__texto {
  font-size: 3rem;
  font-weight: bold;
  color: #FFFFFF;
  margin: 2rem 0;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .header__texto {
    text-align: left;
  }
}
.header__texto--modalidad {
  font-size: 2rem;
}
.header__boton {
  background-color: #00c8c2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  text-transform: uppercase;
  margin: 2rem 0;
}
.header__boton:hover {
  background-color: #009591;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__boton {
    width: auto;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  font-size: 2rem;
  padding: 2rem;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
}
.navegacion__enlace:hover, .navegacion__enlace--actual {
  background-color: #FFFFFF;
  color: #007df4;
}

.barra {
  background-color: #007df4;
}
.barra__contenido {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .barra__contenido {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
.barra__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
}
@media (min-width: 1024px) {
  .barra__logo {
    text-align: left;
    margin: 0;
  }
}

.footer {
  background-color: #007df4;
  margin-top: 5rem;
  padding-top: 3rem;
  color: #FFFFFF;
}
.footer__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(2rem, -0.8rem + 10vw, 4rem);
}
@media (min-width: 1024px) {
  .footer__logo {
    text-align: left;
    margin: 0;
  }
}
.footer__texto {
  line-height: 2;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__texto {
    text-align: left;
  }
}
.footer__copyright {
  background-color: #0063c1;
  margin: 0;
  padding: 2rem 0;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 900;
}
.footer__copyright--regular {
  font-weight: 400;
}

.menu-redes {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 4rem 0;
}
.menu-redes__enlace::before {
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.menu-redes__enlace[href*="facebook.com"]::before {
  content: "\f39e";
}
.menu-redes__enlace[href*="github.com"]::before {
  content: "\f09b";
}
.menu-redes__enlace[href*="tiktok.com"]::before {
  content: "\e07b";
}
.menu-redes__enlace[href*="instagram.com"]::before {
  content: "\f16d";
}
.menu-redes__enlace[href*="youtube.com"]::before {
  content: "\f167";
}
.menu-redes__enlace[href*="twitter.com"]::before {
  content: "\f099";
}
.menu-redes__ocultar {
  display: none;
}
@media (min-width: 768px) {
  .menu-redes {
    justify-content: flex-end;
  }
}

.alerta {
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  text-transform: uppercase;
  margin: 1rem 0;
  font-size: 1.5rem;
  font-weight: bold;
}
.alerta--exito {
  background-color: #78fe78;
  border-left: 0.5rem solid #02db02;
  color: #017601;
}
.alerta--error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}

.speakers__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .speakers__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .speakers__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.speaker {
  border: 1px solid #7f8ea3;
  padding: 3rem;
  background-position: center top;
  background-size: 350px;
  background-repeat: no-repeat;
}
.speaker__nombre {
  font-weight: 900;
  font-size: 2.6rem;
  margin: 2rem 0;
}
.speaker:nth-child(4n+1) {
  background-image: image-set(url("../img/bg_1.avif") 1x, url("../img/bg_1.webp") 1x, url("../img/bg_1.png") 1x);
}
.speaker:nth-child(4n+2) {
  background-image: image-set(url("../img/bg_2.avif") 1x, url("../img/bg_2.webp") 1x, url("../img/bg_2.png") 1x);
}
.speaker:nth-child(4n+3) {
  background-image: image-set(url("../img/bg_3.avif") 1x, url("../img/bg_3.webp") 1x, url("../img/bg_3.png") 1x);
}
.speaker:nth-child(4n+4) {
  background-image: image-set(url("../img/bg_4.avif") 1x, url("../img/bg_4.webp") 1x, url("../img/bg_4.png") 1x);
}
.speaker__ubicacion {
  color: #64748B;
  margin: 1rem 0;
  font-size: 2rem;
  line-height: 1.5;
}
.speaker__imagen {
  min-width: 350px;
}
@media (min-width: 768px) {
  .speaker__imagen {
    min-width: unset;
  }
}
.speaker__listado-skills {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.speaker__listado-skills > li {
  background-color: #007df4;
  color: #FFFFFF;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: bold;
}

.speaker-sociales {
  display: flex;
  gap: 2rem;
  margin-top: 2rem;
}
.speaker-sociales__enlace::before {
  color: #1a1b15;
  font-size: 3rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.speaker-sociales__enlace[href*="facebook.com"]::before {
  content: "\f39e";
}
.speaker-sociales__enlace[href*="github.com"]::before {
  content: "\f09b";
}
.speaker-sociales__enlace[href*="tiktok.com"]::before {
  content: "\e07b";
}
.speaker-sociales__enlace[href*="instagram.com"]::before {
  content: "\f16d";
}
.speaker-sociales__enlace[href*="youtube.com"]::before {
  content: "\f167";
}
.speaker-sociales__enlace[href*="twitter.com"]::before {
  content: "\f099";
}
.speaker-sociales__ocultar {
  display: none;
}

.mapa {
  height: 45rem;
  margin-top: 5rem;
}
.mapa__heading {
  margin: 0;
  text-align: left;
}
.mapa__texto {
  font-size: 1.8rem;
  margin: 0;
}

.boletos__grid {
  width: min(95%, 60rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}

.boleto {
  width: min(100% - 5rem, 65rem);
  padding: 6rem;
  margin: 0 auto;
  border-radius: 2rem;
  color: #FFFFFF;
  position: relative;
}
.boleto::before, .boleto::after {
  position: absolute;
  content: "";
  background-color: #FFFFFF;
  width: 5rem;
  height: 5rem;
  top: calc(50% - 2.5rem);
}
.boleto::before {
  clip-path: circle(50% at 25% 50%);
  left: 0;
}
.boleto::after {
  clip-path: circle(50% at 75% 50%);
  right: 0;
}
.boleto--presencial {
  background-color: hsl(178deg, 100%, 39%);
  background-image: radial-gradient(at 62% 26%, hsl(209deg, 100%, 47%) 0px, transparent 50%), radial-gradient(at 12% 75%, hsl(189deg, 100%, 56%) 0px, transparent 50%), radial-gradient(at 31% 88%, hsl(209deg, 100%, 47%) 0px, transparent 50%), radial-gradient(at 80% 50%, hsl(178deg, 100%, 39%) 0px, transparent 50%), radial-gradient(at 32% 39%, hsl(209deg, 100%, 47%) 0px, transparent 50%), radial-gradient(at 4% 17%, hsl(178deg, 100%, 39%) 0px, transparent 50%), radial-gradient(at 0% 0%, hsl(209deg, 100%, 47%) 0px, transparent 50%);
}
.boleto--virtual {
  background-color: #00c8c2;
}
.boleto--gratis {
  background-color: #007df4;
}
.boleto__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(2rem, -0.8rem + 10vw, 4rem);
}
@media (min-width: 1024px) {
  .boleto__logo {
    text-align: left;
    margin: 0;
  }
}
.boleto__plan {
  font-size: 2rem;
  text-transform: uppercase;
  margin: 3rem 0 0 0;
}
.boleto__precio {
  margin: 3rem 0 0 0;
  font-weight: 900;
  font-size: 4rem;
}
.boleto__enlace-contenedor {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
}
.boleto__enlace {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
}
.boleto__enlace:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.boleto--acceso {
  width: 30rem;
  height: 50rem;
  padding: 10rem 3rem;
}
@media (min-width: 768px) {
  .boleto--acceso {
    width: 60rem;
    height: 30rem;
    padding: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.boleto--acceso .boleto__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
  margin: 0;
  text-align: left;
}
.boleto--acceso .boleto__codigo {
  font-size: 4rem;
  font-weight: 700;
  text-align: center;
  position: absolute;
  bottom: 7rem;
  margin: 0;
  left: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .boleto--acceso .boleto__codigo {
    transform: rotate(90deg);
    width: 30rem;
    transform-origin: 0 0;
    top: 0;
    left: calc(100% - 5rem);
  }
}
.boleto--acceso .boleto__nombre {
  font-size: 2.4rem;
  text-transform: uppercase;
  font-weight: 900;
}
.boleto::before {
  clip-path: circle(50% at 50% 25%);
  left: calc(50% - 2.5rem);
  top: 0;
}
@media (min-width: 768px) {
  .boleto::before {
    clip-path: circle(50% at 25% 50%);
    left: 0;
    top: calc(50% - 2.5rem);
  }
}
.boleto::after {
  clip-path: circle(50% at 50% 75%);
  left: calc(50% - 2.5rem);
  top: calc(100% - 5rem);
}
@media (min-width: 768px) {
  .boleto::after {
    clip-path: circle(50% at 75% 50%);
    left: calc(100% - 5rem);
    top: calc(50% - 2.5rem);
  }
}